body {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url('https://images.pexels.com/photos/1148820/pexels-photo-1148820.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260');
  background-size: cover;
  height: 100vh;
  width: 100%;
}
.root {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  color: white;
  text-align: center;
  font-size: 3rem;
  text-shadow: 0px 1px 3px rgba(0, 0, 0, 0.6);
}

.container {
  width: 300px;
  border-radius: 17px;
  padding: 10px;
  overflow: hidden;
  display: flex;
  background: white;
  backdrop-filter: blur(50px);
  -webkit-box-shadow: 9px 9px 9px -2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 9px 9px 9px -2px rgba(0, 0, 0, 0.75);
  box-shadow: 9px 9px 9px -2px rgba(0, 0, 0, 0.75);
  border: 1px solid rgba(95, 95, 95, 0.377);
}

.table-container {
  width: 1400px;
  border-radius: 17px;
  padding: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  background: white;
  backdrop-filter: blur(50px);
  -webkit-box-shadow: 9px 9px 9px -2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 9px 9px 9px -2px rgba(0, 0, 0, 0.75);
  box-shadow: 9px 9px 9px -2px rgba(0, 0, 0, 0.75);
  border: 1px solid rgba(95, 95, 95, 0.377);
  margin-top: 10px;
}

.pagination {
  margin-top: 10px;
  width: 230px;
  border-radius: 17px;
  padding: 10px;
  overflow: hidden;
  background: white;
  backdrop-filter: blur(50px);
  -webkit-box-shadow: 9px 9px 9px -2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 9px 9px 9px -2px rgba(0, 0, 0, 0.75);
  box-shadow: 9px 9px 9px -2px rgba(0, 0, 0, 0.75);
  border: 1px solid rgba(95, 95, 95, 0.377);
}

form {
  padding: 20px;
}
